import { Message } from 'ant-design-vue';

const state = {
    systemName: '', // 
    date: '', //  
    dataLoading: true, //  
    pageLoading: false, //  
    environmentData: { //  
        env: '', // 0: 1:
        title: ''
    },
    asyncExportNoticePop: { //  
        visible: false,
        file: '', //  
        title: '' //  html
    }
};

const getters = {
};

const mutations = {
    SET_DATA_LOADING_STATUS(state, data) { //  loading
        state.dataLoading = data;
    },
    SET_PAGE_LOADING_STATUS(state, data) { //  loading
        state.pageLoading = data;
    },
    SET_DATE(state, data){ //  
        state.date = new Date(data);
    },
    SET_ENVIRONMENT_DATA(state, data){ //  
        state.environmentData.env = data.env;
        if (data.env == '0'){
            state.environmentData.title = '';
        } else if (data.env == '1'){
            state.environmentData.title = '';
        }
    },
    SET_TABLE_LOADING_START(state, data) { //  
        state.tableLoadingStatus = true;
    },
    SET_TABLE_LOADING_END(state, data) { //  
        state.tableLoadingStatus = false;
    },
    SET_ASYNC_EXPORT_DATA(state, data) { //  
        if (data.action == 'async') {
            if (data.title) {
                state.asyncExportNoticePop.title = data.title;
            }
            state.asyncExportNoticePop.file = data.message;
            state.asyncExportNoticePop.visible = true;
        } else if (data.action == 'sync') {
            window.open(data.url, '_blank');
            // Message.success({
            //     title: '',
            //     type: 'success'
            // });
        }
    },
    SET_ASYNC_POP_STATUS(state, data){ //  
        state.asyncExportNoticePop.visible = data;
    }
};

const actions = {
};

export default {
    state,
    getters,
    actions,
    mutations
};



